<script setup>
import Button from "@/components/tools/Button.vue"
import {onMounted, ref} from 'vue'
import { useWindowScroll } from '@vueuse/core'
const { y } = useWindowScroll()

const animateblock = ref()
const contant = ref()
onMounted(()=>{
    window.addEventListener('scroll',update)
    function update(){
      var startAnimation = 9200
      var opacityPosition = 8700
      var animateOpacity = (y.value - opacityPosition)
      var animatePosition = (y.value - startAnimation)
      var opacityTitle = 1
      var opacityIntro = 0
      var opacityOutiline = 0
      if(animatePosition >= 596){
        animatePosition = 596
      }
      if(animateOpacity > 0 ){
        if(animateOpacity > 240){
            opacityOutiline +=(animateOpacity / 1000)
        }
        opacityTitle -= (animateOpacity / 120)
        opacityIntro += (animateOpacity / 200)
        this.document.getElementById('animate-title').style.opacity = `${opacityTitle }`
        this.document.getElementById('animate-intro').style.opacity = `${opacityIntro}`
        this.document.getElementById('animate-outiline').style.opacity = `${opacityOutiline}`
      }
      if( animatePosition > 0){
      contant.value.style.transform = `translateY(${ -(animatePosition / 1.2 )}px)`
      this.document.getElementById('featurcard').style.transform = `rotateZ( ${animatePosition / 40}deg) translate3d(10px, -${animatePosition / 10}px, 10px)`
      } 
      else{
        animatePosition = 0
      }

    

    }
})



</script>